import Vue from 'vue'
import Vuex from 'vuex'
import api from './../api'

Vue.use(Vuex)

// Vuex  
// state 
// store  store
// mutations  
// actions    

const store = new Vuex.Store({
	state: {
    count: 6,
    todos: [
      { id: 1, text: '...', done: true },
      { id: 2, text: '...', done: false },
      { id: 3, text: '...', done: true },
    ]
	},
  mutations: {
    indexSet(state, data){
      state[data['target']] = data.data
    }
  },
  actions: {
    fetchProject({commit}, params){
      return api.post('/movie/in_theaters', params).then(res => {
        commit('indexSet', {
          target: 'project',
          data: res.data
        })
        return Promise.resolve()
      }, res => {
        console.log('fail')
        return Promise.reject()
      })
    },
  }
})

export default store